import { SimpleFilter, Filter } from './models';

export function abbreviateNumber(value: number) {
  let newValue: any = value;
  if (value >= 1000) {
    const suffixes = ['', 'k', 'm', 'b', 't'];
    const suffixNum = Math.floor(('' + value).length / 3);
    let shortValue = 0;
    for (let precision = 2; precision >= 1; precision--) {
      shortValue = parseFloat((suffixNum !== 0 ? (value / Math.pow(1000, suffixNum)) : value).toPrecision(precision));
      const dotLessShortValue = (shortValue + '').replace(/[^a-zA-Z 0-9]+/g, '');
      if (dotLessShortValue.length <= 2) { break; }
    }
    // if (shortValue % 1 != 0) shortNum = shortValue.toFixed(1);
    newValue = shortValue + suffixes[suffixNum];
  }
  return newValue;
}

export function arraysEqual($1, $2) {
  if ($1 === $2) { return true; }
  if ($1 == null || $2 == null) { return false; }
  if ($1.length !== $2.length) { return false; }

  // If you don't care about the order of the elements inside
  // the array, you should sort both arrays here.
  // Please note that calling sort on an array will modify that array.
  // you might want to clone your array first.

  for (let i = 0; i < $1.length; ++i) {
    if ($1[i] !== $2[i]) { return false; }
  }
  return true;
}

/** Sort array of objects */
export function sortBy(key: string) {
  const reverse = key.indexOf('-') !== -1;
  const k = reverse ? key.slice(1) : key;
  return ($1, $2) => {
    if ($1[k] < $2[k]) { return reverse ? 1 : -1; }
    if ($1[k] > $2[k]) { return reverse ? -1 : 1; }
    return 0;
  };
}


/** @returns return data without duplicates */
export function withoutDupsSimpleFilter(data: SimpleFilter[] = []) {
  const out = [];
  const history = {};
  for (const element of data) {
    if (history[element.label]) { continue; }
    out.push(element);
    history[element.label] = 1;
  }
  return out;
}

export function withoutDupsFilters(data: Filter[] = []) {
  const out = [];
  const history = {};
  for (const element of data) {
    if (history[element.keyword]) { continue; }
    out.push(element);
    history[element.keyword] = 1;
  }
  return out;
}

export function paginate(array, page_size, page_number) {
  --page_number; // because pages logically start with 1, but technically with 0
  return array.slice(page_number * page_size, (page_number + 1) * page_size);
}
