import { Component, OnInit, Output, EventEmitter, Input } from '@angular/core';
import { FormControl } from '@angular/forms';
import { HttpClient } from '@angular/common/http';

import { Filter } from '../_shared/models';
import { AppService } from '../_services/app.service';
import { CUSTOM_EVENT_RESET_FILTERS } from '../_shared/constants';

@Component({
  selector: 'ap-mode-bar',
  templateUrl: './mode-bar.component.html',
  styleUrls: ['./mode-bar.component.scss']
})
export class ModeBarComponent implements OnInit {

  // datepicker values
  date_from = new FormControl(new Date());
  date_to = new FormControl(new Date());

  // search string
  search_ctrl = new FormControl();

  json_data_loaded = false;

  // search options
  options = [
    'best foundation',
    'cookware',
    'couch',
    'credit card',
    'employee survey',
    'Harley Davidson',
    'invoice template',
    'life insurance',
    'luggage',
    'managed transportation service',
    'nursing scrubs',
    'running shoes',
    'solder station',
    'stem cell therapy',
    'vacuum pumps'
  ];
  filtered_options = [];

  @Output() search: EventEmitter<Filter[]> = new EventEmitter<Filter[]>();
  @Output() loading: EventEmitter<boolean> = new EventEmitter<boolean>(false);

  constructor(
    private http: HttpClient,
    public service: AppService
  ) { }

  ngOnInit() {
    this.handle_search();
  }

  private handle_search() {
    this.search_ctrl.valueChanges.subscribe(async (value: string) => {
      if (!value || typeof value !== 'string') { return; }
      this.filter(value);
      if (!this.options.includes(value)) { return; }
      this.service.setTitle(value);
      this.service.current_search_term = value;
      const url = value.trim().toLowerCase().replace(/\s|\s./g, '-');
      this.load_json(url);
    });
  }

  private async load_json(url: string) {
    try {
      this.handle_loading(true);
      const json: any = await this.http.get(`./api/${url}.json`).toPromise();
      this.search.emit(json);
      this.handle_loading(false);
    } catch (error) {
      this.handle_loading(false);
      if (error && error.status !== 404) { console.error(error); }
    }
  }

  private handle_loading(status: boolean = false) {
    const content: HTMLElement = document.querySelector('.mat-drawer-content');
    if (content) { content.style.overflow = status ? 'hidden' : 'auto'; }
    this.loading.emit(status);
  }

  reset() {
    this.search_ctrl.setValue(null);
    this.service.selected_keywords = [];
    this.json_data_loaded = false;
    this.filtered_options = [];
    AppService.dispatchEvent(CUSTOM_EVENT_RESET_FILTERS);
  }

  private filter(name: string) {
    const filterValue = name.toLowerCase();
    this.filtered_options = this.options.filter((option: string) => option.toLowerCase().indexOf(filterValue) === 0);
  }

}
