import { Injectable } from '@angular/core';
import { Title } from '@angular/platform-browser';

import { Keyword } from '../_shared/models';

@Injectable({
  providedIn: 'root'
})
export class AppService {

  readonly mainTitle: string = 'Ad Planner';

  constructor(private title: Title) { }

  keyword_ideas = 0;

  selected_keywords: Keyword[] = [];
  current_search_term: string = null;

  public static dispatchEvent(event: string): void {
    const evt = new Event(event);
    document.dispatchEvent(evt);
  }

  setTitle(title?: string) {
    const t = title ? `${this.mainTitle} - ${title}` : this.mainTitle;
    this.title.setTitle(t);
  }

}
