import { MediaMatcher } from '@angular/cdk/layout';
import { Component, ChangeDetectorRef, OnDestroy, AfterViewInit, ViewChild } from '@angular/core';
import { MatTableDataSource, MatPaginator } from '@angular/material';
import { SelectionModel } from '@angular/cdk/collections';
import { AppService } from './_services/app.service';
import { Filter } from './_shared/models';
import { withoutDupsFilters } from './_shared/helper';

/**
 * Buit with
 * https://material.angular.io/
 * https://www.highcharts.com/
 * https://material.io/tools/icons/
 * PWA - https://angular.io/guide/service-worker-getting-started
 */

@Component({
  selector: 'ap-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent implements AfterViewInit, OnDestroy {
  mobileQuery: MediaQueryList;

  loading = false;
  panelOpenState = false;
  filter_data = [];
  filtered_data: Filter[] = [];

  @ViewChild('paginator') paginator: MatPaginator;
  dataSource: MatTableDataSource<Filter>;
  selection = new SelectionModel<Filter>(true, []);

  // sample data
  fillerNav: Array<Array<string>> = [
    [
      'Keyword ideas',
      'Grouped ideas'
    ],
    [
      'Plan overview',
      'Ad groups',
      'Keywords',
      'Locations'
    ]
  ];

  private _mobileQueryListener: () => void;

  columns: string[] = [
    'select',
    'Keyword (by relevance)',
    'Avg.monthly searches',
    'Competition',
    'Ad impression share',
    'Top of page bid(low range)',
    'Top of page bid(high range)',
    'Account status'
  ];

  constructor(
    changeDetectorRef: ChangeDetectorRef,
    media: MediaMatcher,
    public service: AppService
  ) {
    this.mobileQuery = media.matchMedia('(max-width: 600px)');
    this._mobileQueryListener = () => changeDetectorRef.detectChanges();
    this.mobileQuery.addListener(this._mobileQueryListener);
  }

  ngOnDestroy() {
    this.mobileQuery.removeListener(this._mobileQueryListener);
  }

  ngAfterViewInit() {
  }

  /** Whether the number of selected elements matches the total number of rows. */
  isAllSelected() {
    if (!this.dataSource) { return false; }
    const numSelected = this.selection.selected.length;
    const numRows = this.dataSource.data.length;
    return numSelected === numRows;
  }

  /** Selects all rows if they are not all selected; otherwise clear selection. */
  masterToggle() {
    if (!this.dataSource) { return false; }
    this.isAllSelected() ?
      this.selection.clear() :
      this.dataSource.data.forEach(row => this.selection.select(row));
  }

  /** The label for the checkbox on the passed row */
  checkboxLabel(row?: Filter): string {
    if (!row) {
      return `${this.isAllSelected() ? 'select' : 'deselect'} all`;
    }
    return `${this.selection.isSelected(row) ? 'deselect' : 'select'} row ${row.keyword}`;
  }

  getCompetition(value): string {
    const n = +value;
    if (typeof n !== 'number') { return value; }
    return (value < 1) ? 'High' : 'Low';
  }

  getNonExcludedKeywordsCount() {
    return this.service.selected_keywords.filter(i => !i.excluded).length;
  }

  onFiltered(filtered: Filter[]) {
    const data = withoutDupsFilters(filtered);
    // Assign the data to the data source for the table to render
    this.dataSource = new MatTableDataSource(data);
    this.dataSource.paginator = this.paginator;
    this.filtered_data = data;
  }
}
