import {
  Component, OnInit, Input, OnChanges, SimpleChanges,
  ViewChildren, QueryList, Output, EventEmitter
} from '@angular/core';
import { PageEvent, MatExpansionPanel, MatCheckbox, MatSlideToggle } from '@angular/material';

import { SimpleFilter, FilterGroup, Filter, Keyword } from '../_shared/models';
import { abbreviateNumber, sortBy, withoutDupsSimpleFilter, paginate } from '../_shared/helper';
import { AppService } from '../_services/app.service';
import { CUSTOM_EVENT_RESET_FILTERS } from '../_shared/constants';

/**
  O/ /kwd|kwd/ig
  A/ /^(?=.*kwd)(?=.*kwd).*$/im
  
  | previous Group | Current Group | Operator |
  |----------------|---------------|----------|
  |      NULL      |       1       |    AND   | -> A/First selection
  |       1        |       1       |    OR    | -> O/Same group selection
  |       1        |       0       |    AND   | -> A/Groups through selection

 * */

@Component({
  selector: 'ap-refine-keywords',
  templateUrl: './refine-keywords.component.html',
  styleUrls: ['./refine-keywords.component.scss'],
  host: { class: 'filters' }
})
export class RefineKeywordsComponent implements OnInit, OnChanges {

  readonly page_size: number = 10;
  event_id = null;

  // this holds all filter data.
  groups_map = {};
  hash_map_kwd = {};
  selected_keywords_map = {};

  // grouped data for Refine keywords
  filter_groups: Array<FilterGroup> = [];
  excluded_checkboxes: Array<MatCheckbox> = [];

  // Input json data from mode-bar component
  @Input() filter_data: Filter[] = [];
  // Output filtered data
  @Output() filtered: EventEmitter<Filter[]> = new EventEmitter<Filter[]>();

  expanded = false;
  expanding = false;

  @ViewChildren('filter_panel') filter_panel: QueryList<MatExpansionPanel>;
  @ViewChildren('mat_checkbox') mat_checkboxes: QueryList<MatCheckbox>;
  @ViewChildren('select_all_attr') select_all_attrs: QueryList<MatSlideToggle>;

  previous_group: string = null;
  checkbox_color = 'primary';

  constructor(
    public service: AppService
  ) { }

  ngOnInit() {
    this.listenForReset();
  }

  private listenForReset() {
    document.addEventListener(CUSTOM_EVENT_RESET_FILTERS, () => {
      this.filter_groups.map(f => {
        f.filters.map(i => i.checked = false);
        this.selectAllInGroup(f, false);
      });

      const sliders = this.select_all_attrs.toArray() || [];
      for (const slider of sliders) { slider.checked = false; }
    });
  }

  // handle filters onInput changes
  ngOnChanges(changes: SimpleChanges) {
    const data: Filter[] = (changes && changes.filter_data && changes.filter_data.currentValue) || [];
    this.buildFilterGroups(data);
  }

  /**
   * Make filter groups (Refine keywords)
   * @param data JSON data
   */
  private buildFilterGroups(data: Filter[] = []): void {
    this.filter_groups = []; // Refine keyword groups

    if (!data.length) { return; }
    this.filter_data = data;

    /** @returns filter counts and results for each group */
    const getCounts = (filters: Filter[] = []) => {
      const table = {};
      for (let i = 0, length = filters.length; i < length; i++) {
        const element = filters[i];
        this.hash_map_kwd[element.concept_name] = element.group_name;
        if (!table[element.group_name]) { table[element.group_name] = { __results: 1 }; }

        if (table[element.group_name][element.concept_name]) {
          table[element.group_name][element.concept_name]++;
          this.service.keyword_ideas += table[element.group_name].__results++;
        } else {
          table[element.group_name][element.concept_name] = 1;
        }
      }
      return table;
    };

    const counts = getCounts(data);

    for (const key in counts) {
      if (counts.hasOwnProperty(key)) {
        // try to keep brand/s at the begining of the groups
        if (/^brands|brand$/i.test(key)) {
          this.addFilterGroup(key, counts, data, true);
          continue;
        }
        this.addFilterGroup(key, counts, data);
      }
    }
    // load all data to the table.
    // NOTE: We should send data inside set timeout
    clearTimeout(this.event_id);
    this.event_id = setTimeout(() => this.sendData(data), 500);
  }

  private addFilterGroup(key: string, counts: {}, data: Filter[], add_in_front: boolean = false) {
    const filter_group: FilterGroup = {};
    filter_group.title = key;
    filter_group.results = counts[key] ? counts[key].__results : 0;
    const filters: SimpleFilter[] = data
      .filter(item => item.group_name === key)
      .map(f => ({ label: f.concept_name, enabled: true, results: counts[f.group_name][f.concept_name] }));
    this.groups_map[key] = withoutDupsSimpleFilter(filters).sort(sortBy('-results'));
    filter_group.filters = this.groups_map[key].slice(0, this.page_size);
    if (add_in_front) {
      this.filter_groups.unshift(filter_group);
    } else {
      this.filter_groups.push(filter_group);
    }
  }

  // show some filters in subtitles on filters
  private showSampleFilters(data: SimpleFilter[] = []) {
    return data.map(i => i.label).join(', ');
  }

  private showSelectedFilters(filter_group: FilterGroup) {
    const checked = filter_group.filters.filter(i => i.checked).map(i => `${i.label}`);
    const excluded = filter_group.filters.filter(i => !i.enabled).map(i => `<strike>${i.label}</strike>`);
    return [...checked, ...excluded].join(', ');
  }

  private showExcludedFilters(item: FilterGroup) {
    return item.filters.filter(i => !i.enabled).map(i => i.label).join(', ');
  }

  // effective when paginating on filters
  public handlePageChange(group: FilterGroup = {}, event: PageEvent) {
    const groups: Array<any> = this.groups_map[group.title] || [];
    group.filters = paginate(groups, event.pageSize, event.pageIndex + 1);
  }

  public hasFilters(): boolean {
    return !!this.filter_groups.length;
  }

  // expand one filter group
  private expandOne(panel: MatExpansionPanel, callback) {
    if (typeof callback !== 'function') { return; }
    // return at the end of list
    if (!panel) { return callback(true); }
    // skip if the panel already expanded
    if (panel.expanded) { return callback(false); }
    // expand the panel
    panel.expanded = true;
    // call the callback when the panel expanded
    panel.afterExpand.subscribe(res => callback(false));
  }

  // expand all filter groups
  public expandAll() {
    // return if the expanding process in progress
    if (this.expanding) { return; }

    // begin expandings
    this.expanding = true;
    let count = 0;

    const results: MatExpansionPanel[] = this.filter_panel.toArray();
    const panel: MatExpansionPanel = results[count++];

    // local function which handles after expand callback
    const handleDone = (done) => {
      if (done) { return this.expanding = false; }
      this.expandOne(results[count++], handleDone);
    };

    this.expandOne(panel, handleDone);
  }

  // effective when selecting checkboxes on filters
  private handleSelection(value: string, checked: boolean, excluded: boolean, select_all_attr: MatSlideToggle) {
    setTimeout(() => {
      if (checked) {
        this.updateSelectedKeywords(value, excluded);
      } else {
        this.removeFromSelectedKeywords(value);
        if (select_all_attr) { select_all_attr.checked = false; }
      }

      // load data to the table
      this.loadDataToTable();
    });
  }

  private updateSelectedKeywords(value: string, excluded: boolean) {
    let found = false;
    for (let i = 0, length = this.service.selected_keywords.length; i < length; i++) {
      if (this.service.selected_keywords[i].name === value) {
        this.service.selected_keywords[i].excluded = excluded;
        found = true;
        break;
      }
    }
    if (!found) { this.service.selected_keywords.push({ name: value, excluded }); }
  }

  private removeFromSelectedKeywords(value: string) {
    // just for run at the end of the stack
    setTimeout(() => this.service.selected_keywords.splice(this.service.selected_keywords.findIndex(v => v.name === value), 1));
  }

  private loadDataToTable() {
    setTimeout(() => {
      // get selected keyword names
      const selected_keywords: string[] = [];
      const keyword_groups: Array<string[]> = [];
      const excluded_keywords: Keyword[] = [];
      const filtered_data: Filter[] = [];

      for (const kwd of this.service.selected_keywords) {
        selected_keywords.push(kwd.name);
        if (kwd.excluded) { excluded_keywords.push(kwd); }
      }

      const groups = Object.keys(this.groups_map);

      if (!selected_keywords.length) { return this.sendData(this.filter_data); }

      // grouping selected keywords for each group
      for (let i = 0, length = groups.length; i < length; i++) {
        const group_name = groups[i];
        const keywords = this.groups_map[group_name]
          .filter(k => k.checked && k.enabled)
          .map(f => f.label);
        if (!keywords.length) { continue; }
        keyword_groups.push(keywords);
      }

      // searching for matching keywords
      if (!keyword_groups.length && !excluded_keywords.length) { return; }

      const patterns = keyword_groups.map(k => k.join('|'));
      const pattern = this.buildPattern(patterns);

      for (let i = 0, length = this.filter_data.length; i < length; i++) {
        const filter: Filter = this.filter_data[i];
        if (excluded_keywords.length && this.isExcluded(filter.keyword, excluded_keywords)) { continue; }
        if (pattern.test(filter.keyword) || selected_keywords.includes(filter.concept_name)) {
          filtered_data.push(filter);
        }
      }

      // emit filtered data
      this.sendData(filtered_data);
    });
  }

  private sendData(filtered_data: Filter[]) {
    this.filtered.emit(filtered_data);
  }

  private isExcluded(keyword: string, excluded_keywords: Keyword[]) {
    if (!excluded_keywords.length) { return false; }
    const keywords_str = excluded_keywords.map(i => i.name).join('|');
    const pattern = new RegExp(keywords_str, 'gi');
    return pattern.test(keyword);
  }

  private buildPattern(keywords: string[]) {
    //const pattern_body = keywords.map(k => `(?=.*${k})`).join('');
    const pattern_body = keywords.join('|');
    return new RegExp(`^${pattern_body}.*$`, 'ig');
  }

  public getInclusionsCount(item: FilterGroup): number {
    return item.filters.filter(i => i.checked).length;
  }

  public getExclusionsCount(item: FilterGroup): number {
    return item.filters.filter(i => !i.enabled).length;
  }

  // select all checkboxes in a group
  public selectAllInGroup(filter_group: FilterGroup, checked: boolean) {
    const iterate: any = f => {
      f.checked = checked;
      this.calculateSelections();
      this.handleSelection(f.label, f.checked, false, null);
    };
    this.groups_map[filter_group.title].map(iterate);
  }

  private calculateSelections() {
    for (const filter_group of this.filter_groups) {
      filter_group.selections = 0;
      for (let i = 0, length = filter_group.filters.length; i < length; i++) {
        const filter = filter_group.filters[i];
        if (filter.checked) {
          filter_group.selections += filter.results;
        }
      }
    }
  }

  public getTotalAttributes(item: FilterGroup) {
    let count = 0;
    item.filters.map(i => count += i.results);
    return abbreviateNumber(count);
  }

  public getFilterGroupSubtitle(filter_panel: MatExpansionPanel, filter_group: FilterGroup) {
    const selected = this.showSelectedFilters(filter_group);

    if (selected.length) {
      return `${this.getInclusionsCount(filter_group)} selected, ${this.getExclusionsCount(filter_group)} excluded.`;
    }
    return `<b>eg: </b> ${this.showSampleFilters(filter_group.filters.slice(0, 3))}${filter_group.filters.length > 3 ? '...' : null}`;
  }

  public handleCheckboxChange(filter: SimpleFilter, filter_group: FilterGroup, select_all_attr: MatSlideToggle) {
    filter.enabled = true;

    this.handleSelection(filter.label, filter.checked, false, select_all_attr);
    this.calculateSelections();
    this.updateSelectedKeywordsMap(filter_group);
  }

  private updateSelectedKeywordsMap(filter_group: FilterGroup) {
    this.selected_keywords_map[filter_group.title] = filter_group.filters;
  }

  public handleCheckboxClick(filter: SimpleFilter, filter_group: FilterGroup) {
    if (!filter.enabled) {
      setTimeout(() => {
        filter.checked = false;
        this.removeFromSelectedKeywords(filter.label);
        this.calculateSelections();
      }, 100);
    }
  }

  public exclude(mat_checkbox: MatCheckbox, filter: SimpleFilter, filter_group: FilterGroup, select_all_attr: MatSlideToggle) {
    filter.enabled = false;
    filter.checked = false;

    this.calculateSelections();
    this.updateSelectedKeywordsMap(filter_group);
    this.updateSelectedKeywords(filter.label, true);
    this.loadDataToTable();
    if (select_all_attr) { select_all_attr.checked = false; }
  }

}
