import { Component, OnInit, AfterViewInit } from '@angular/core';

declare const Highcharts;

@Component({
  selector: 'ap-trends-chart',
  templateUrl: './trends-chart.component.html',
  styleUrls: ['./trends-chart.component.scss']
})
export class TrendsChartComponent implements OnInit, AfterViewInit {

  constructor() { }

  ngOnInit() {
  }

  ngAfterViewInit() {
    this.initChartView();
  }

  // https://www.highcharts.com/demo/column-parsed
  private initChartView(): void {
    // tslint:disable-next-line: no-string-literal
    if (!window['Highcharts']) { return; }
    Highcharts.chart('trends-chart-container', {
      data: {
        table: 'trends-datatable'
      },
      credits: {
        enabled: false
      },
      exporting: { enabled: false },
      chart: {
        type: 'column',
        reflow: true
      },
      title: {
        text: 'Trends'
      },
      yAxis: {
        allowDecimals: false,
        title: {
          text: 'Units'
        }
      },
      tooltip: {
        formatter() {
          return '<b>' + this.series.name + '</b><br/>' +
            this.point.y + ' ' /* + this.point.x */;
        }
      }
    });
  }

}
